.add_pro_con {
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: 60px;
    width: 80vw;
    background-color: white;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    padding-top: 30px;
    border: solid 1px rgb(221, 221, 221);
}

.add_pro_det {
    width: 50%;
    display: flex;
    flex-direction: column;

    justify-content: space-evenly;
    align-items: baseline;

    button {
        margin-top: 20px;
        margin-bottom: 30px;
        color: white;
        padding: 10px 25px;
        font-size: 1rem;
    }
}
.preview {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    h2 {
        transform: translateY(-50px);
        font-weight: 300;
    }
}

.lable_inp_pair {
    font-size: 1.2rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    // border: solid 1px black;
    margin: 5px;
    width: 80%;
    input {
        font-size: 0.9rem;
        padding: 3px 5px;
        width: 200px;
        margin: 3px;
    }
    select {
        font-size: 0.9rem;
        padding: 3px 20px;
    }
}

#pro_img {
    font-size: 0.9rem;
    // padding: 10px;
    // color: white;
}
textarea {
    resize: none;
}

.row_pair {
    display: flex;
    flex-direction: row;
    width: 100%;
    margin: 1px auto;
    justify-content: space-evenly;
}
label {
    margin: 5px;
}

#pro_des {
    width: 100%;
    height: 40px;
}
#addpro_canbtn {
    border: solid 1px $secondary;
    color: black;
    border-radius: 20px;
    background-color: white;
}
#addpro_addbtn {
    display: inline;
    background-color: $secondary;
    border-radius: 20px;
    color: white;
    height: 40px;
    border: none;
    outline: none;
    cursor: pointer;
    margin-top: 20px;
    margin-bottom: 30px;
    color: white;
    padding: 10px 25px;
    font-size: 1rem;
}

// input[type="file"] {
