@media screen and (max-width: 720px) {
    .add_pro_con {
        text-align: center;
        margin-left: auto;
        margin-right: auto;
        margin-top: 60px;
        width: 95vw;
        background-color: white;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        padding-top: 30px;
        border: solid 1px rgb(221, 221, 221);
    }
    .preview {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        height: 520px;

        h2 {
            font-weight: 300;
        }
    }

    .add_pro_det {
        width: 100%;
        display: flex;
        flex-direction: column;

        justify-content: space-evenly;
        align-items: center;

        button {
            margin-top: 20px;
            margin-bottom: 30px;
            color: white;
            padding: 10px 25px;
            font-size: 1rem;
        }
    }
    .lable_inp_pair {
        font-size: 1.2rem;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
        // border: solid 1px black;
        margin: 5px;
        width: 100%;
        input {
            font-size: 0.9rem;
            padding: 3px 5px;
            width: 80%;
            margin: 3px;
        }
    }
    .row_pair {
        display: flex;
        flex-direction: column;
        width: 100%;
        margin: 1px auto;
        justify-content: space-evenly;
        // padding-left: 30px;
    }
    #btnrow {
        flex-direction: column-reverse;
    }
}
